//-----------------------------------------------------------
// KICKSTART (USED ON EVERY PROJECT)
//-----------------------------------------------------------

@import "001-kickstart/_helpers";
// reset + standard helper classes
@import "001-kickstart/_mixins";
// common mixins

//-----------------------------------------------------------
// LIBS
//-----------------------------------------------------------

@import "008-libs/bootstrap/bootstrap-grid";                           // styles that need to be refactored, or that are hacky because of browser compatibility etc.
@import "008-libs/slick/slick.css";                  //slick


//-----------------------------------------------------------
// PROJECT BASE
//-----------------------------------------------------------

@import "002-base/_fonts";
// @font-face declarations
@import "002-base/_variables";
// color, font, breakpoint variables, etc
@import "002-base/_base";
// base styles for project
@import "002-base/_layout";
// useful layout styles
@import "002-base/_typography";
// type styles for project
@import "002-base/_utilities";
// misc reusables - small chunks as opposed to entire sections like modules
@import "002-base/_grid";
// grid

//-----------------------------------------------------------
// PATTERNS
//-----------------------------------------------------------

@import "003-patterns/sprite";                      // svg sprite to css
@import "003-patterns/_buttons";                    // button styles for project
@import "003-patterns/_forms";                      // form styles
@import "003-patterns/_icons.scss";                 // icon styles
@import "003-patterns/_modules.scss";               // larger modules that are reused throughout the site
@import "003-patterns/_modals.scss";                // modal styles

//-----------------------------------------------------------
// PAGES
//-----------------------------------------------------------

@import "005-pages/common/_common";                  // common
@import "005-pages/home/home";                       // home

//-----------------------------------------------------------
// THIRD PARTY STYLES
//-----------------------------------------------------------

@import "006-third-party/_wp.scss";                 // wordpress-specific styles

//-----------------------------------------------------------
// SHAME
//-----------------------------------------------------------

@import "007-shame/_shame.scss";                    // styles that need to be refactored, or that are hacky because of browser compatibility etc.

//-----------------------------------------------------------
// GLOBAL ELEMENTS (HEADER & FOOTER, ETC)
//-----------------------------------------------------------

@import "004-globals/header";                      // header styles
@import "004-globals/footer";                      // footer styles
@import "004-globals/responsive";



